.nav__content {
    display: flex;
    justify-content: space-between;
}

.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    /* transition-timing-function: ease-in; */
    transition: all 0.5s ease-in;
}

.nav__black {
    background-color: #111;
}

.nav__logo {
    position: fixed;
    left: 0;
    top: 25px;
    width: 80px;
    cursor: pointer;
    padding-left: 20px;
    object-fit: contain;
}

.nav__avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}